import React from "react";
import { useEffect } from "react";
import {
  Route,
  Routes,
  useNavigate,
  useLocation,
  Navigate,
} from "react-router-dom";

import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import EditProfilePopup from "./EditProfilePopup";
import EditAvatarPopup from "./EditAvatarPopup";
import AddPlacePopup from "./AddPlacePopup";
import ImagePopup from "./ImagePopup";
import InfoToolTip from "./InfoToolTip";
import Login from "./Login";
import Register from "./Register";
import ProtectedRoute from "./ProtectedRoute";
import api from "../utils/api";
import { CurrentUserContext } from "../contexts/CurrentUserContext";
import { tokenValidate, register, login } from "../utils/auth";

function App() {
  const [selectedCard, setSelectedCard] = React.useState({});
  const [currentUser, setCurrentUser] = React.useState({});
  const [cards, setCards] = React.useState([]);
  const [loggedIn, setLoggedIn] = React.useState(false);
  const [email, setEmail] = React.useState("");

  const navigate = useNavigate();
  const location = useLocation();

  const handleTokenCheck = (path) => {
    if (localStorage.getItem("jwt")) {
      const token = localStorage.getItem("jwt");
      // проверяем токен пользователя
      tokenValidate(token)
        .then((res) => {
          if (res) {
            setLoggedIn(true);
            navigate(path);

            setEmail(res.data.email);
          }
        })
        .catch((err) => console.log(err));
    }
  };

  const handleLogin = ({ password, email }) => {
    login({
      password,
      email,
    })
      .then((res) => {
        if (res) {
          setLoggedIn(true);
          setEmail(email);

          localStorage.setItem("jwt", res.token);

          navigate("/");
        } else {
          setIsSuccess(false);
          setisInfoToolTipOpen(true);
        }
      })
      .catch((err) => {
        setIsSuccess(false);
        setisInfoToolTipOpen(true);
        console.log(err);
      });
  };

  const handleLogout = (evt) => {
    evt.preventDefault();

    localStorage.removeItem("jwt");
    setLoggedIn(false);
    navigate("/login");
  };
  //Запрос данных с сервера
  useEffect(() => {
    handleTokenCheck(location.pathname);
    Promise.all([api.getUserInfo(), api.getCards()])
      .then(([dataProfile, dataUser]) => {
        setCurrentUser(dataProfile);
        setCards(dataUser);
        console.log("dataUser", dataProfile);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  //функция обновления данных User

  const handleUpdateUser = ({ name, about }) => {
    api
      .changeUserInfo({ name, about })
      .then((data) => {
        setCurrentUser(data);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  };
  //функция обновления данных avatar

  const handleUpdateAvatar = ({ avatar }) => {
    api
      .updateUserAvatar(avatar)
      .then((data) => {
        setCurrentUser(data);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  //Обработчики кликов
  const handleEditAvatarClick = () => {
    setIsEditAvatarPopupOpen(!isEditAvatarPopupOpen);
  };
  const handleEditProfileClick = () => {
    setIsEditProfilePopupOpen(!isEditProfilePopupOpen);
  };
  const handleAddPlaceClick = () => {
    setIsAddPlacePopupOpen(!isAddPlacePopupOpen);
  };
  const handleCardClick = (card) => {
    setSelectedCard(card);
  };

  //Добавление карточки
  const handleAddPlaceSubmit = (data) => {
    api
      .insertCard(data)
      .then((newCard) => {
        setCards([newCard, ...cards]);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  //лайк на карточке
  function handleCardLike(card) {
    const isLiked = card.likes.some((i) => i._id === currentUser._id);

    api
      .changeLikeCardStatus(card._id, !isLiked)
      .then((newCard) => {
        setCards((state) =>
          state.map((c) => (c._id === card._id ? newCard : c))
        );
      })
      .catch((err) => {
        console.log(err);
      });
  }

  // Удаление карточки
  function handleCardDelete(card) {
    api
      .deleteCard(card._id)
      .then(() => {
        setCards((state) => state.filter((c) => c._id !== card._id));
      })
      .catch((err) => {
        console.log(err);
      });
  }

  //стейты
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] =
    React.useState(false);
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] =
    React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const [isInfoToolTipOpen, setisInfoToolTipOpen] = React.useState(false);
  const [isSuccess, setIsSuccess] = React.useState(false);

  //Закртыие попапов
  const closeAllPopups = () => {
    setIsEditProfilePopupOpen(false);
    setIsAddPlacePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setisInfoToolTipOpen(false);
    setSelectedCard({});
  };

  //хук закрытия
  const useClosePopupByEscape = (isOpen) => {
    const closeByEsc = (e) => {
      if (e.key === "Escape") {
        closeAllPopups();
      }
    };
    useEffect(() => {
      if (isOpen) {
        document.addEventListener("keydown", closeByEsc);
        return () => document.removeEventListener("keydown", closeByEsc);
      }
    }, [isOpen]);
  };
  const useClosePopup = (
    overlayClassName,
    closeButtonClassName,
    onClose,
    isOpen
  ) => {
    const handleClose = (e) => {
      if (
        e.target.classList.contains(overlayClassName /*"popup_opened"*/) ||
        e.target.classList.contains(closeButtonClassName /*"popup__exit"*/)
      ) {
        onClose();
      }
    };
    useEffect(() => {
      if (isOpen) {
        document.addEventListener("click", handleClose);
        return (e) => {
          document.removeEventListener("click", handleClose);
        };
      }
    }, [isOpen]);
  };

  const handleRegister = ({ password, email }) => {
    register({
      password,
      email,
    })
      .then((res) => {
        if (res) {
          setIsSuccess(true);
          setisInfoToolTipOpen(true);
          navigate("/");
        } else {
          setIsSuccess(false);
          setisInfoToolTipOpen(true);
        }
      })
      .catch((err) => {
        setIsSuccess(false);
        setisInfoToolTipOpen(true);
        console.log(err);
      });
  };

  return (
    <CurrentUserContext.Provider value={{ currentUser }}>
      <div className="page">
        <div className="wrapper">
          <Header
            loggedIn={loggedIn}
            location={location.pathname}
            onLogout={handleLogout}
            email={email}
          />
          <Routes>
            <Route
              path="/sign-up"
              element={<Register register={handleRegister} />}
            />
            <Route path="/sign-in" element={<Login login={handleLogin} />} />
            <Route
              exact
              path="/"
              element={
                <ProtectedRoute loggedIn={loggedIn}>
                  <Main
                    onEditProfile={handleEditProfileClick}
                    onAddPlace={handleAddPlaceClick}
                    onEditAvatar={handleEditAvatarClick}
                    onCardClick={handleCardClick}
                    onCardLike={handleCardLike}
                    onCardDelete={handleCardDelete}
                    currentUser={currentUser}
                    cards={cards}
                  />
                </ProtectedRoute>
              }
            />
            <Route path="*" element={<Navigate to="/" />} />
          </Routes>

          <Footer />
        </div>
        <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups}
          useClosePopup={useClosePopup}
          useClosePopupByEscape={useClosePopupByEscape}
          onUpdateUser={handleUpdateUser}
          currentUser={currentUser}
        />
        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          useClosePopup={useClosePopup}
          useClosePopupByEscape={useClosePopupByEscape}
          onUpdateAvatar={handleUpdateAvatar}
        />
        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups}
          useClosePopup={useClosePopup}
          useClosePopupByEscape={useClosePopupByEscape}
          onAddPlace={handleAddPlaceSubmit}
        />
        <InfoToolTip
          isOpen={isInfoToolTipOpen}
          onClose={closeAllPopups}
          useClosePopup={useClosePopup}
          useClosePopupByEscape={useClosePopupByEscape}
          isSuccess={isSuccess}
        />

        {/* <PopupWithForm
            name="submit"
            title="Вы уверены"
            isOpen={isRemovePlacePopupOpen}
            onClose={closeAllPopups}
            useClosePopup={useClosePopup}
            useClosePopupByEscape={useClosePopupByEscape}
          >
            <button
              className="popup__button popup__button_type_submit"
              type="submit"
              aria-label="кнопка Удаление"
            >
              Да
            </button>
          </PopupWithForm> */}
        <ImagePopup
          card={selectedCard}
          onClose={closeAllPopups}
          useClosePopup={useClosePopup}
          useClosePopupByEscape={useClosePopupByEscape}
          isOpen={Boolean(Object.keys(selectedCard).length)}
        />
      </div>
    </CurrentUserContext.Provider>
  );
}

export default App;
