import React from "react";
// import { useEffect } from "react";
import agree from "../image/agree.svg";
import failure from "../image/failure.svg";

function InfoToolTip({
  onClose,
  useClosePopup,
  isOpen,
  useClosePopupByEscape,
  isSuccess,
}) {
  useClosePopup("popup_opened", "popup__exit", onClose, isOpen);
  useClosePopupByEscape(isOpen);

  return (
    <>
      <div className={`popup  ${isOpen ? "popup_opened" : ""} `}>
        <div className="popup__container">
          <button
            className={"popup__exit popup__exit_type_auth"}
            type="button"
          ></button>
          <form className={`popup__form `} name="popup__form" noValidate>
            {/* <img className="header__logo" src={logo} alt="Лого" /> */}
            <img
              className="popup__auth-img"
              src={isSuccess ? agree : failure}
              alt="Лого"
            />

            <h2 className={`popup__title_type_auth`}>
              {isSuccess
                ? "Вы успешно зарегистрировались"
                : `Что-то пошло не так! Попробуйте ещё раз.`}
            </h2>
          </form>
        </div>
      </div>
    </>
  );
}

export default InfoToolTip;
