import React from "react";
import logo from "../image/__logo.svg";

function Header({ loggedIn, location, onLogout, email }) {
  let text = "";
  let href = "";
  if (location === "/sign-in") {
    text = "Регистрация";
    href = "/sign-up";
  }
  if (location === "/sign-up") {
    text = "Войти";
    href = "/sign-in";
  }
  if (loggedIn && location === "/") {
    text = "Выйти";
  }
  return (
    <header className="header">
      <img className="header__logo" src={logo} alt="Лого" />
      <div className="header__wrapper">
        {loggedIn && email && <div className="header__email">{email}</div>}
        <a
          className="auth__button-enter"
          href={href}
          onClick={loggedIn ? onLogout : null}
        >
          {text}
        </a>
      </div>
    </header>
  );
}

export default Header;
